<template>
  <v-row class="mt-10">
    <v-treeview :items="items">
      <template v-slot:label="{ item }">
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <span v-bind="attrs" v-on="on">
              <span :class="item.color">{{ item.name }}</span>
              <span v-if="item.parts">"</span>
              <span
                v-for="part in item.parts"
                :key="part.id"
                :class="
                  part.added ? 'green--text' : part.removed ? 'red--text text-decoration-line-through' : 'black--text'
                "
                >{{ part.value }}</span
              >
              <span v-if="item.parts">"</span>
            </span>
          </template>
          <v-row>
            <v-col v-if="item.parts">
              <span
                v-for="part in item.parts"
                :key="part.id"
                :class="
                  part.added ? 'green--text' : part.removed ? 'red--text text-decoration-line-through' : 'white--text'
                "
                >{{ part.value }}</span
              >
            </v-col>
            <v-col v-if="item.before" class="text--lighten-3 red--text">{{ item.before }}</v-col>
            <v-col v-if="item.after" class="text--lighten-2 green--text">{{ item.after }}</v-col>
            <v-col v-if="item.both" class="white--text">{{ item.both }}</v-col>
          </v-row>
        </v-tooltip>
      </template>
    </v-treeview>
  </v-row>
</template>

<script>
export default {
  name: 'TreeWithStyles',
  props: {
    items: Array
  }
}
</script>