<template>
  <v-container>
    <v-radio-group v-model="filterSelected" row @change="filterTable">
      <v-radio label="Todo" value="todo"></v-radio>
      <v-radio label="Seguridad" value="seguridad"></v-radio>
      <v-radio label="Métodos" value="metodos"></v-radio>
      <v-radio label="Parámetros" value="parametros"></v-radio>
    </v-radio-group>
    <v-row class="mt-10">
      <v-col>
        <v-data-table
          :items="filteredItemsTable"
          item-key="id"
          :headers="headersTable"
          class="fixed-width-table"
          show-expand
        >
          <!-- vetur uses old eslint version and won't allow to use v-slot modifier -->
          <!-- eslint-disable-next-line -->
          <template v-slot:item.parts="{ item }">
            <span
              v-for="part in item.parts"
              :key="part.id"
              :class="
                part.added ? 'green--text' : part.removed ? 'red--text text-decoration-line-through' : 'black--text'
              "
              >{{ part.value }}</span
            >
          </template>
          <!-- eslint-disable-next-line -->
          <template v-slot:item.action="{ item }">
            <span
              :class="
                item.action === 'Añadido' ? 'green--text' : item.action === 'Eliminado' ? 'red--text' : 'black--text'
              "
              >{{ item.action }}</span
            >
          </template>
          <template v-slot:expanded-item="{ headers, item }">
            <td :colspan="headers.length">
              <span
                v-for="part in item.fullParts"
                :key="part.id"
                :class="
                  part.added ? 'green--text' : part.removed ? 'red--text text-decoration-line-through' : 'black--text'
                "
                >{{ part.value }}</span
              >
            </td>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
const SeguridadRegex = /x-/
const MetodosRegex = /\.paths \.\/(\w|\{|\}|\/)*( \.\w+)?$/
const ParametrosRegex = /\.parameters/

export default {
  name: 'TableWithFilter',
  props: {
    itemsTable: Array
  },
  data: () => ({
    filterSelected: 'todo',
    filteredItemsTable: [],
    headersTable: [
      { text: 'Ruta', value: 'path', width: '33%' },
      { text: 'Acción', value: 'action', width: '33%' },
      { text: 'Diferencia', value: 'parts', width: '34%' }
    ]
  }),
  methods: {
    filterTable() {
      this.filteredItemsTable = this.itemsTable.filter((element) => {
        let passFilters = false
        if (this.filterSelected === 'todo') {
          passFilters = true
        }
        if (this.filterSelected === 'seguridad') {
          if (SeguridadRegex.test(element.path)) passFilters = true
        }
        if (this.filterSelected === 'metodos') {
          if (MetodosRegex.test(element.path)) passFilters = true
        }
        if (this.filterSelected === 'parametros') {
          if (ParametrosRegex.test(element.path)) passFilters = true
        }
        return passFilters
      })
    },
    cleanTable() {
      this.filteredItemsTable = []
    },
    resetFilter(items) {
      this.filterSelected = 'todo'
      this.filteredItemsTable = items
    }
  }
}
</script>

<style>
.fixed-width-table table {
  table-layout: fixed;
}
</style>