import {
    v4 as uuidv4
} from 'uuid'
import * as differ from 'diff'

const COLOR = {
    MODIFICADO: 'orange--text',
    NUEVO: 'green--text',
    ELIMINADO: 'red--text',
    NORMAL: 'black--text',
    NORMAL_CLARO: 'white--text'
}

export default {
    compareSwaggersObject(node1, node2, dest, path, addToTable, itemsTable) {
        let equals = true
        if (Array.isArray(node1) && Array.isArray(node2)) {
            equals = this.compareSwaggersArray(node1, node2, dest, path, addToTable, itemsTable)
        } else {
            let node1Attrs = Object.keys(node1)
            let node2Attrs = Object.keys(node2)

            for (let attr of node1Attrs) {
                if (!node2Attrs.includes(attr)) {
                    equals = false
                    let nodeIsObject = typeof node1[attr] === 'object'
                    this.addDeletedElementToTree(attr, dest, nodeIsObject, node1[attr], path, itemsTable)
                    this.addDeletedElementToTable(attr, path, addToTable, JSON.stringify(node1[attr], null, 1), '', itemsTable)
                } else {
                    let nodeIsObject = typeof node1[attr] === 'object'
                    let childrenEquals = this.addModifiedElementToTree(attr, dest, nodeIsObject, node1[attr], node2[attr], path, itemsTable)
                    equals &= childrenEquals
                    let addToTableChild = addToTable && !nodeIsObject && !childrenEquals
                    this.addModifiedElementToTable(attr, path, addToTableChild, node1[attr], node2[attr], itemsTable)
                }
            }

            for (let attr of node2Attrs) {
                if (!node1Attrs.includes(attr)) {
                    equals = false
                    let nodeIsObject = typeof node2[attr] === 'object'
                    this.addNewElementToTree(attr, dest, nodeIsObject, node2[attr], path, itemsTable)
                    this.addNewElementToTable(attr, path, addToTable, '', JSON.stringify(node2[attr], null, 1), itemsTable)
                }
            }
        }
        return equals
    },
    compareSwaggersArray(node1, node2, dest, path, addToTable, itemsTable) {
        let equals = true

        for (let attr of node1) {
            let attr2 = node2.find((e) => e.name === attr.name)
            if (!attr2) {
                equals = false
                let nodeIsObject = typeof attr === 'object'
                this.addDeletedElementToTree('[i]', dest, nodeIsObject, attr, path, itemsTable)
                this.addDeletedElementToTable('[i]', path, addToTable, JSON.stringify(attr, null, 1), '', itemsTable)
            } else {
                let nodeIsObject = typeof attr === 'object'
                let childrenEquals = this.addModifiedElementToTree('[i]', dest, nodeIsObject, attr, attr2, path, itemsTable)
                equals &= childrenEquals
                let addToTableChild = addToTable && !nodeIsObject && !childrenEquals
                this.addModifiedElementToTable('[i]', path, addToTableChild, attr, attr2, itemsTable)
            }
        }

        for (let attr2 of node2) {
            let attr = node1.find((e) => e.name === attr2.name)
            if (!attr) {
                equals = false
                let nodeIsObject = typeof attr2 === 'object'
                this.addNewElementToTree('[i]', dest, nodeIsObject, attr2, path, itemsTable)
                this.addNewElementToTable('[i]', path, addToTable, '', JSON.stringify(attr2, null, 1), itemsTable)
            }
        }
        return equals
    },
    addDeletedElementToTree(attr, dest, isObject, oldValue, path, itemsTable) {
        let itemChild = {
            id: uuidv4(),
            name: attr,
            color: COLOR.ELIMINADO
        }
        if (isObject) {
            itemChild.children = []
            this.compareSwaggersObject(oldValue, {}, itemChild.children, `${path} .${attr}`, false, itemsTable)
        } else {
            itemChild.name += `: "${oldValue}"`
        }
        dest.push(itemChild)
    },
    addNewElementToTree(attr, dest, isObject, newValue, path, itemsTable) {
        let itemChild = {
            id: uuidv4(),
            name: attr,
            color: COLOR.NUEVO
        }
        if (isObject) {
            itemChild.children = []
            this.compareSwaggersObject({}, newValue, itemChild.children, `${path} .${attr}`, false, itemsTable)
        } else {
            itemChild.name += `: "${newValue}"`
        }
        dest.push(itemChild)
    },
    addModifiedElementToTree(attr, dest, isObject, oldValue, newValue, path, itemsTable) {
        let itemChild = {
            id: uuidv4(),
            name: attr
        }
        dest.push(itemChild)
        let childrenEquals
        if (isObject) {
            itemChild.children = []
            childrenEquals = this.compareSwaggersObject(oldValue, newValue, itemChild.children, `${path} .${attr}`, true, itemsTable)
            let color = childrenEquals ? COLOR.NORMAL : COLOR.MODIFICADO
            itemChild.color = color
        } else {
            itemChild.name += ': '
            childrenEquals = oldValue === newValue
            let color = childrenEquals ? COLOR.NORMAL : COLOR.MODIFICADO
            itemChild.color = color
            itemChild.parts = differ.diffWords(oldValue.toString(), newValue.toString())
        }
        return childrenEquals
    },
    addDeletedElementToTable(attr, path, addToTable, oldValue, newValue, itemsTable) {
        if (addToTable) {
            let fullParts = [{
                removed: true,
                value: oldValue.toString()
            }]
            let subParts = this.filterFirstCharactersOfParts(fullParts, 200);
            itemsTable.push({
                id: uuidv4(),
                path: `${path} .${attr}`,
                action: 'Eliminado',
                parts: subParts,
                fullParts: fullParts
            })
        }
    },
    addNewElementToTable(attr, path, addToTable, oldValue, newValue, itemsTable) {
        if (addToTable) {
            let fullParts = [{
                added: true,
                value: newValue.toString()
            }]
            let subParts = this.filterFirstCharactersOfParts(fullParts, 200);
            itemsTable.push({
                id: uuidv4(),
                path: `${path} .${attr}`,
                action: 'Añadido',
                parts: subParts,
                fullParts: fullParts
            })
        }
    },
    addModifiedElementToTable(attr, path, addToTable, oldValue, newValue, itemsTable) {
        if (addToTable) {
            let fullParts = differ.diffWords(oldValue.toString(), newValue.toString())
            let subParts = this.filterFirstCharactersOfParts(fullParts, 200);
            itemsTable.push({
                id: uuidv4(),
                path: `${path} .${attr}`,
                action: 'Modificado',
                parts: subParts,
                fullParts: fullParts
            })
        }
    },
    filterFirstCharactersOfParts(fullParts, numberOfCharacters) {
        let filteredParts = []
        let cuttedPart = false
        fullParts.reduce((acc, element) => {
            let newAcc = acc + element.value.length
            if (newAcc < numberOfCharacters) filteredParts.push(element)
            else {
                cuttedPart = true
                let newElement = {}
                Object.assign(newElement, element)
                newElement.value = newElement.value.substring(0, numberOfCharacters - acc)
                filteredParts.push(newElement)
            }
            return newAcc
        }, 0)
        if (filteredParts.length !== fullParts.length || cuttedPart) {
            filteredParts.push({
                value: '...'
            })
        }
        return filteredParts
    }
}