<template>
  <v-container>
    <SwaggerInput :swaggers="swaggers" @swaggersChanged="cleanItems" />
    <v-row class="mb-10" justify="center">
      <v-btn @click="compareSwaggers" :disabled="!swaggers.swagger1 || !swaggers.swagger2">Comparar</v-btn>
    </v-row>
    <v-divider />
    <v-tabs v-model="tab" grow>
      <v-tab key="0">Tabla</v-tab>
      <v-tab key="1">Árbol</v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item key="0" eager>
        <TableWithFilter :itemsTable="itemsTable" ref="tableWithFilter" />
      </v-tab-item>
      <v-tab-item key="1" eager>
        <TreeWithStyles :items="itemsTree" />
      </v-tab-item>
    </v-tabs-items>
  </v-container>
</template>

<script>
import SwaggerInput from '@/components/SwaggerInput'
import TableWithFilter from '@/components/TableWithFilter'
import TreeWithStyles from '@/components/TreeWithStyles'
import compareService from '@/services/compare'

export default {
  name: 'SwaggerCompare',
  components: {
    SwaggerInput,
    TableWithFilter,
    TreeWithStyles
  },
  data: () => ({
    itemsTree: [],
    itemsTable: [],
    tab: 0,
    swaggers: {
      swagger1: null,
      swagger2: null
    }
  }),
  methods: {
    cleanItems() {
      this.itemsTree = []
      this.itemsTable = []
      this.$refs.tableWithFilter.cleanTable()
    },
    compareSwaggers() {
      this.items = []
      this.itemsTable = []
      this.$refs.tableWithFilter.cleanTable()
      compareService.compareSwaggersObject(
        this.swaggers.swagger1,
        this.swaggers.swagger2,
        this.itemsTree,
        '',
        true,
        this.itemsTable
      )
      this.$refs.tableWithFilter.resetFilter(this.itemsTable)
    }
  }
}
</script>